
	/* eslint-disable */
	import ContentBase from '../components/ContentBase.vue'
	import {
		ref
	} from 'vue'
	import {
		ElMessageBox
	} from 'element-plus'

	const dialogVisible = ref(false)
	const value = ref()
	const value1 = ref()
	const value2 = ref()
	const handleClose = (done: () => void) => {
		ElMessageBox.confirm('Are you sure to close this dialog?')
			.then(() => {
				done()
			})
			.catch(() => {
				// catch error
			})
	}

	export default {
		name: "AboutView",
		components: {
			ContentBase,
		},
		data() {
			return {
				value,
				value1,
				value2,
				dialogVisible
			}
		}
	};
